<template lang='pug'>
  #app.container
    h4 ul with transition-group
    draggable.group(:list='list', :tag='"ul"', :draggable-flag='draggableFlag', hasHandle)
      transition-group(name='flip-list')
        draggable-item.list-group-item(v-for='item in list', :key='item', :tag='"li"')
          drag-handle.mr-2
          span {{item}}
    button.btn.btn-primary.mr-2(type="button" @click='toggleDraggable') toggle draggable
    span now: {{ draggableFlag }}
    hr.mb-5
    h4 table tbody, 利用 scope slot 重写 dragHandle 的内容
    table.table
      thead
        th 操作
        th 水果
      draggable(:list='list', :tag='"tbody"', :draggable-flag='draggableFlag', hasHandle)
        draggable-item(v-for='item in list', :key='item', :tag='"tr"')
          td
            drag-handle
              template(slot-scope="dragHandle")
                button.btn.btn-outline-info(@mouseover='dragHandle.enableDrag', @mouseleave='dragHandle.disableDrag') ☰
            td {{ item }}
</template>

<script>
import 'bootstrap/dist/css/bootstrap.min.css'

export default {
  name: 'app',
  data () {
    return {
      list: ["apple", "banana", "pinapple", "grape", "strawberry"],
      draggableFlag: false,
    }
  },
  methods: {
    toggleDraggable () { // 切换是否可拖拽
      this.draggableFlag = !this.draggableFlag
    }
  }
}
</script>

<style scoped>
.flip-list-move {
  transition: transform 1s;
}
</style>